import * as React from 'react';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import FavoriteIcon from '@mui/icons-material/Favorite';
import { Home, Person } from '@mui/icons-material';
import MenuIcon from '@mui/icons-material/Menu';
import { NavLink } from 'react-router-dom';
import SideBar from './Menu';

export default function BotNav() {
  const [value, setValue] = React.useState('recents');
  const [open, setOpen] = React.useState(false);
  const toggleDrawer = () => {
    setOpen(!open);
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
    <BottomNavigation sx={{ width:"100%",position: 'fixed', bottom: 0, left: 0, right: 0 }} value={value} onChange={handleChange}>
      <BottomNavigationAction
      component ={NavLink} 
      to="shoppingcart/"
        value="home"
        icon={<Home/>}
      />
      <BottomNavigationAction
      sx={{"&:hover":{color:"red"}}}
        value="favorites"
        icon={<FavoriteIcon />}
      />
      <BottomNavigationAction
        value="profile"
        icon={<Person/>}
      />
      <BottomNavigationAction  value="menu" onClick={toggleDrawer} icon={<MenuIcon />} />
    </BottomNavigation>
    <SideBar toggleDrawer={toggleDrawer} active={open}/>
    </>
  );
}